import React from 'react';
import ToggleButton from 'react-toggle-button';
import { alertBox } from '../../commonRedux/';
import Button from '../../components/Button';
import GigFilter from '../../components/Filter/gigFilter';
import Gig from '../../components/Gigs/Gig';
import GigRequest from '../../components/Gigs/GigRequest';
import Modal from '../../components/Popup';
import RewardsWidget from '../../components/RewardsWidget';
import { allGigRequests, getGigs } from '../../http/gig-calls';
import { postReport, postUnReport } from '../../http/http-calls';
import './gigs.css';

class AllGigRequests extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            limit: 20,
            gigPage:false,
            page: 1,
            cid: 0,
            posts: [],
            category: '',
            reason: '',
            reportModal:false,
            data: {
                category: "",
                subCategory : "",
                priceFrom : "",
                priceTo : "",
                rating : "",
                key: ""
            },
            lastData: null,
            lastI: null,
            lastType:null,
            gigs: [],
            currentTab:0
        }
    }

    reportModal = (p = null, i = null, t=null) => {
        this.setState({
            lastData: p,
            lastI: i,
            lastType: t
        });
        this.setState({reportModal: !this.state.reportModal}) // true/false toggle
    };

    report = () => {
            var lastData = this.state.lastData;
            var lastI = this.state.lastI;
            var link = "gigonomy/gig/"+lastData._id;
            postReport({ id: lastData._id, type: this.state.lastType, link, category: this.state.category, reason: this.state.reason })
            .then(async resp => {
                alertBox(true, resp.message, 'success');
                var temp = this.state.posts;
                temp[lastI].reported = 1;
                this.setState({ reportModal: false, posts: temp });
            }, error => {
                alertBox(true, error.data.message);
                this.setState({ reportModal: false});
            });
    
    };
    unReport = (post, i) => {
        postUnReport({ id: post._id })
        .then(async resp => {
            alertBox(true, resp.message, 'success');
            var temp = this.state.posts;
            temp[i].reported = 0;
            this.setState({ reportModal: false, posts: temp });
        }, error => {
            alertBox(true, error.data.message);
        });
    };

    componentDidMount = () => {
        this.getRequests();
    }

    dataChange = (data = {}) => {  
        this.getRequests(data);
        
    }

    getRequests = (data = {}) => {
        this.setState({
            posts: []
        })
        var d = data;
        d['page']  = this.state.page;
        d['limit'] = this.state.limit;
        d['userid'] = this.props.currentUser._id;
        allGigRequests(data).then(resp => {
          this.setState({
            posts: resp
          })
        }, err => {
  
        });
    }


    render() {
        return (
            <div className="gigsPage">
                <Modal displayModal={this.state.reportModal} closeModal={this.reportModal} >
                     <div>
                         <div className="form-group">
                             <select className="form-control" value={this.state.category}  onChange={(e) => this.setState({category: e.target.value})}>
                                 <option value="">Select Category</option>
                                 <option>Violence</option>
                                 <option>Racism / Hatespeech</option>
                                 <option>Pornographic</option>
                                 <option>Spam</option>
                                 <option>Other</option>
                             </select>
                         </div>
                         <div className="form-group">
                             <textarea type="text" placeholder="Reason" className="form-control" onChange={(e) => this.setState({reason: e.target.value})} value={this.state.reason} />
                         </div>
                     </div>
                <div className="">
                <Button variant="primary" size="compact m-2" onClick={() => this.report()}>Report</Button>
                <Button variant="secondary" size="compact m-2" onClick={this.reportModal}>Cancel</Button>
                </div>
             </Modal>
            <div className="container my-wall-container ">
                
              <div className="row mt-2">
                {/* <!-- left column --> */}
                <div className="col-sm empty-container-with-out-border left-column">
                <GigFilter dataChange={this.dataChange} />
                </div>
                {/* <!-- end left column --> */}

                {/* <!-- center column --> */}
                <div className="col-sm empty-container-with-out-border center-column">
                    <div className="banner">
                        <div className="banner-body">
                            <div className="banner-desc">
                                <h3>All Talents</h3>
                            </div>
                            <div className="banner-btns">
                                <span className="pr-2">Find Jobs</span>
                                    <ToggleButton activeLabel="" inactiveLabel="" value={true} onToggle={(value) => {
                                      if(value){
                                          this.props.history.push("/passionomy/gigs");
                                      }
                                    }} />
                                <span className="pl-2">Hire Talents</span>
                            </div>
                        </div>
                    </div>
        
                    <div className="clearfix">
                    {
                        this.state.posts.length == 0 ?
                        <p className="no-found">No Gig Requests Found</p>
                        :
                        <div className="row-1">
                            {
                        this.state.posts.map((gig, i) => {
                            return <GigRequest {...this.props} post={gig} />
                        })
                    }
                    </div>
                    }
                
                    
                </div>

                
                    
                </div>
                {/* <!-- end center column --> */}

                {/* <!--  right column --> */}
                <div className="col-sm empty-container-with-out-border right-column">
                    <RewardsWidget {...this.props} />
                    {/* <CreateGigWidget extra={true} />
                    <PopularGigs /> */}
                </div>
                {/* <!-- end right column --> */}
              </div>
            </div>

            </div>
        );
    }
}

export default AllGigRequests;